<template>
    <div class="max-w-4xl mx-auto bg-gray-200 p-4 rounded">
        <div class="text-left py-2">
             <router-link to="/admin/feedback-question/create" class="bg-blue-500 text-white rounded p-2">Create Appointment</router-link>
        </div>
        <div class="max-w-2xl grid grid-cols-2 mx-auto">
            <div v-for="(question, index) in feedbackQuestion" :key="index" class="text-left gap-4" >
                {{ question }}
                <Rating :numberOfStar="rating.length" v-model="rating[index]" />
            </div>
        </div>
    </div>
</template>

<script>
import Select from '../../../../../components/select.vue'
import Input from '../../../../../components/input-text.vue'
import Textarea from '../../../../../components/input-text-area.vue'
import Btn from '../../../../../components/btn.vue'
import Rating from "./Rating.vue"
export default {
    components:{
        Select,
        Input,
        Textarea,
        Btn,
        Rating,
    },
     data() {
        return {
            rating: [],
        }
    },

    created(){
        this.$store.dispatch('adminFeedback/getFeedbacks', {
            appointmentId:this.appointmentId
        });
    },
    computed: {
        feedbacks(){
             return this.$store.state.adminFeedback.feedbacks;
        },
        feedbackQuestion() {
            const fbquestions = this.$store.state.adminFeedback.feedbacks;
            return fbquestions.questions ? Object.values(fbquestions.questions.questions) : [];
        },
        appointmentId(){
            return this.$route.params.appointmentId;
        }
    }
}
</script>